import React from 'react'
import PropTypes from 'prop-types' //Se puede colocar con el comando rafcp
import "./Card.css"

const Card = ({lang,img,fcolor,scolor}) => {
    return (                
        //En los estilos si la propiedad leva 2 palabara separados por (- ) se pone asi borderRadius,flexBox
        <div className="card" 
        style={{background:`linear-gradient(to right, ${fcolor}, ${scolor})`}}>
        <img src={img} alt="lang.svg" />      
        <h3>{lang}</h3>
        </div>
    )
};

//Los propTypes sirven para especificar el tipo de propiedad que pasas
Card.prototype = {
    lang: PropTypes.string,
    img: PropTypes.string,
    fcolor: PropTypes.string,
    scolor: PropTypes.string
}

export default Card
