import React, { Fragment } from 'react'
import Card from './Card'

const Cards = () => {
    const data =[
        {
          "lang": "Python",
          "url": "https://cdn.svgporn.com/logos/python.svg",
          "fcolor": "#00c3ff",
          "scolor": "#ffff1c"
        },
        {
          "lang": "Java",
          "url": "https://cdn.svgporn.com/logos/java.svg",
          "fcolor": "#f5af19",
          "scolor": "#f12711"
        },
        {
          "lang": "React",
          "url": "https://cdn.svgporn.com/logos/react.svg",
          "fcolor": "#000428",
          "scolor": "#004e92"
        },
        {
          "lang": "Perl",
          "url": "https://cdn.svgporn.com/logos/perl.svg",
          "fcolor": "#cc2b5e",
          "scolor": "#753a88"
        }
      ];
      
    return (
        // El Fragment permite seprar nuestro componentes, tambien se puede poner vacio
        <Fragment>
            {
                data.map(({lang,url,fcolor,scolor})=>{
                    // El key es el identificador de cada componente
                    return <Card key={lang} lang={lang} img={url} fcolor={fcolor} scolor={scolor}/>         
                })
            }              
        </Fragment>
    )
}

export default Cards
